/* CREATED USING RANGE.CSS */
input.musicplayer__range {
    -webkit-appearance: none;
    width: 100%;
    margin: 0.9px 0;
  }
  input.musicplayer__range:focus {
    outline: none;
  }
  input.musicplayer__range::-webkit-slider-runnable-track {
    width: 100%;
    height: 3.2px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #7f7186;
    border-radius: 0px;
    border: 0.4px solid #7f7186;
  }
  input.musicplayer__range::-webkit-slider-thumb {
    box-shadow: 2.6px 2.6px 3.7px rgba(0, 0, 0, 0), 0px 0px 2.6px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 5px;
    width: 43px;
    border-radius: 50px;
    background: #d4796a;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -1.3px;
  }
  input.musicplayer__range:focus::-webkit-slider-runnable-track {
    background: #8c7e92;
  }
  input.musicplayer__range::-moz-range-track {
    width: 100%;
    height: 3.2px;
    cursor: pointer;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
    background: #7f7186;
    border-radius: 0px;
    border: 0.4px solid #7f7186;
  }
  input.musicplayer__range::-moz-range-thumb {
    box-shadow: 2.6px 2.6px 3.7px rgba(0, 0, 0, 0), 0px 0px 2.6px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 5px;
    width: 43px;
    border-radius: 50px;
    background: #d4796a;
    cursor: pointer;
  }
  input.musicplayer__range::-ms-track {
    width: 100%;
    height: 3.2px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input.musicplayer__range::-ms-fill-lower {
    background: #726578;
    border: 0.4px solid #7f7186;
    border-radius: 0px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  input.musicplayer__range::-ms-fill-upper {
    background: #7f7186;
    border: 0.4px solid #7f7186;
    border-radius: 0px;
    box-shadow: 0px 0px 0px #000000, 0px 0px 0px #0d0d0d;
  }
  input.musicplayer__range::-ms-thumb {
    box-shadow: 2.6px 2.6px 3.7px rgba(0, 0, 0, 0), 0px 0px 2.6px rgba(13, 13, 13, 0);
    border: 0px solid rgba(0, 0, 0, 0);
    height: 5px;
    width: 43px;
    border-radius: 50px;
    background: #d4796a;
    cursor: pointer;
    height: 3.2px;
  }
  input.musicplayer__range:focus::-ms-fill-lower {
    background: #7f7186;
  }
  input.musicplayer__range:focus::-ms-fill-upper {
    background: #8c7e92;
  }
  

